import { useContext, useEffect, useState } from "react";
import Checklogin from "./Checklogin";
import UserContext from "./UserContext";
import axios from "axios";

function Wishlist() {
  const userdata = useContext(UserContext);
  const [products, setProducts] = useState([]);
  const [loggedin, setLoggedin] = useState(Checklogin());

  async function getWishlist() {
    if (!userdata || !userdata.user_id) {
      return;
    }

    const data = new FormData();
    data.append("user_id", userdata.user_id);

    try {
      const response = await axios.post("http://localhost/get-wishlist.php", data, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });

      const result = response.data?.data;
      if (Array.isArray(result)) {
        setProducts(result);
      } else {
        setProducts([]);
      }
    } catch (error) {
      setProducts([]);
    }
  }

  useEffect(() => {
    if (loggedin) {
      getWishlist();
    }
  }, [userdata, loggedin]);

  async function RemoveWishlistItem(product) {
    if (!loggedin || !userdata?.user_id) return;

    const data = new FormData();
    data.append("user_id", userdata.user_id);
    data.append("id", product.id);

    try {
      const response = await axios.post("http://localhost/delete-wishlist.php", data, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });

      if (response.data.status === 'success') {
        getWishlist();
      }
    } catch (error) {}
  }

  return (
    <div className='container mt-4'>
      <h2 className="mb-4">Your wishlist</h2>
      {Array.isArray(products) && products.length > 0 ? (
        <div className='d-flex flex-row flex-wrap'>
          {products.map((product) => (
            <div key={product.id} className='card mx-2 my-2' style={{ width: '18rem' }}>
              <img src={product.images} className='card-img-top' alt={product.name} style={{ height: '150px', objectFit: 'cover' }} />
              <div className='card-body'>
                <p className='card-title'>{product.name}</p>
                <button
                  className='btn btn-danger'
                  onClick={() => RemoveWishlistItem(product)}
                >
                  Remove from wishlist
                </button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <h5>Your Wishlist has no items</h5>
      )}
    </div>
  );
}

export default Wishlist;
